import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { AuthService } from '../auth/auth.service';

@Component({
  selector: 'my-chatbox',
  templateUrl: './mychatbox.component.html',
  styleUrls: ['./mychatbox.component.css'],
  styles: [`
    ::ng-deep nb-layout-column {
      justify-content: center;
      display: flex;
    }
    nb-chat {
      width: 500px;
    }
  `]
})
export class MychatboxComponent implements OnInit, OnDestroy {
  private authListenerSubs: Subscription;
  userIsAuthenticated = false;
  userName:string = "User:";
  answer:string;
  messages: any[] = [];

  constructor(private authservice: AuthService) { }

  ngOnInit(): void {
    this.userIsAuthenticated = this.authservice.getIsAuth();
    this.authListenerSubs = this.authservice
    .getAuthStatusListener()
    .subscribe( isAuthenticated => {
      this.userIsAuthenticated = isAuthenticated;
    });
  }

  onLogout(){
    this.authservice.logout();
  }

  ngOnDestroy(){
    this.authListenerSubs.unsubscribe();
  }

  openForm() {
    let chatwindowstatus=document.getElementById("myForm").style.display
    if (chatwindowstatus == "none")
    {
      document.getElementById("myForm").style.display = "block";
    }
    else
    {
      document.getElementById("myForm").style.display = "none";
    }

  }
  
}
